/* ----------------HEADER AREA---------------- */
#header{
    width: 100%;
    height: 100vh;
    /*
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    */

    /* newly added */
    transition: opacity 1s ease, transform 1s ease;
}

/* intro animation */

#header.hidden {
    pointer-events: none;
    transform: translateY(2rem); /* Move header down */
    opacity: 0; /* Fade out header */
    transition: opacity 1s ease, transform 1s ease;
}

.container{
    padding: 10px 10%;
}



/* header text */
.header-text{
    margin-top: 30%;
    font-size: 30px;
}
.header-text h1{
    font-size: 60px;
    margin-top: 10px;
}
.header-text h1 span{
    color: #ff004f;
}

/* small screens */

@media only screen and (max-width: 600px){
    .header-text{
        margin-top: 100%;
        font-size: 16px;
    }
    .header-text h1{
        font-size: 30px;
    }
}